.boardXS,
.boardS,
.boardM,
.boardL,
.boardXL {
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  perspective: 1000px;
}

.boardXS {
  width: 440px;
  height: 450px;
}
.boardS {
  width: 660px;
  height: 600px;
}
.boardM {
  width: 990px;
  height: 600px;
}
.boardL {
  width: 1320px;
  height: 600px;
}
.boardXL {
  width: 1100px;
  height: 900px;
}

.card {
  width: calc(25% - 10px);
  height: calc(33.333% - 10px);
  margin: 5px;
  position: relative;
  border-radius: 5px;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
  transform: scale(1);
  transform-style: preserve-3d;
  transition: transform 0.5s;
}

.card:active {
  transform: scale(0.97);
  transition: transform 0.2s;
}

.frontFace,
.backFace {
  width: 100%;
  height: 100%;
  padding: 20px;
  position: absolute;
  border: 1px solid rgb(15, 11, 11);
  border-radius: 5px;
  backface-visibility: hidden;
}

.backFace {
  background-color: #fde4c6;
}

.frontFace {
  transform: rotateY(180deg);
}

.flip {
  transform: rotateY(180deg);
}
